






























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import {mapActions} from 'vuex';

@Component({
  methods: {
    ...mapActions([
      'getNotebooks',
      'addNotebook',
      'updateNotebook',
      'deleteNotebook',
      'checkLogin'
    ])
  }
})
export default class NotebookList extends Vue {
  getNotebooks!: Function;
  addNotebook!: Function;
  updateNotebook!: Function;
  deleteNotebook!: Function;
  checkLogin!: Function;

  get notebooks() {
    return this.$store.getters.notebooks;
  }

  created() {
    this.checkLogin('/login');
    this.getNotebooks();
  }

  onCreate() {
    this.$prompt('请输入笔记本标题', '创建笔记本', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      inputPattern: /^.{1,30}$/,
      inputErrorMessage: '标题不能为空，且不超过30个字符'
    }).then((res: any) => {
      const {value} = res;
      this.addNotebook({title: value});
    });
  }

  onEdit(notebook: { title: string; id: string }) {
    this.$prompt('请输入笔记本标题', '修改笔记本', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      inputPattern: /^.{1,30}$/,
      inputValue: notebook.title,
      inputErrorMessage: '标题不能为空，且不超过30个字符'
    }).then((res: any) => {
      const {value} = res;
      this.updateNotebook({notebookId: notebook.id, title: value});
    });
  }

  onDelete(notebook: { id: string }) {
    this.$confirm('您确定要删除吗？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      this.deleteNotebook({notebookId: notebook.id});
    });
  }
}
